@mixin transition {
  transition: all 500ms ease-in-out; }

@mixin flex-col {
  display: flex;
  flex-direction: column; }

@mixin flex-row {
  display: flex;
  flex-direction: row; }

@mixin section-mobile-margin {
  margin: 3rem auto; }

@mixin section-lg-padding {
  padding: 3rem 0; }

@mixin line-height-1rem-text {
  line-height: 1.3rem; }

@mixin d-none {
  display: none; }
