.header {
  height: 667px;
  width: 100vw;
  position: relative;
  // background:
  //   image: url('../../img/header-bg.svg')
  //   position: center center
  //   repeat: no-repeat
  //   size: cover

  .bg-img {
    position: absolute;
    top: 0; }

  .header-text-container {
    @include flex-col;
    justify-content: center;
    align-items: center;
    height: 70%; }

  .header-text {
    text-align: center;
    color: $secondary;
    @include flex-col;
    justify-content: center;
    align-items: center;

    .main-title {
      text-transform: uppercase;
      font-size: 2rem;
      line-height: 2.5rem; }

    .subtitle {
      font-size: 1rem; } } }

@media screen and (min-width: 667px) {
  .header {
    height: 1112px;
    background: {
      image: url('../../img/header-md-bg.svg');
      position: center center;
      repeat: no-repeat;
      size: cover; }

    .bg-img {
      display: none; }

    .header-text-container {
      align-items: flex-start; }

    .header-text {
      text-align: left;
      justify-content: flex-start;
      align-items: flex-start;

      .main-title {
        font-size: 2.5rem !important;
        line-height: 3rem !important; }

      .subtitle {
        font-size: 1.25rem !important; } } } }

@media screen and (min-width: 850px) {
  .header {
    height: 800px;
    background: {
      image: url('../../img/header-lg-bg.svg'); } } }

@media screen and (min-width: 1300px) {
  .header {
    height: 1000px;

    .header-text {

      .main-title {
        font-size: 3rem !important;
        line-height: 3.5rem !important; }

      .subtitle {
        font-size: 1.5rem !important; } } } }
