.navbar {
  background: transparent;
  padding: $gutterWidth; }

.nav-flex {
  display: flex;
  align-items: center; }

.brand-logo {
  margin-right: auto;
  width: 50px;
  z-index: 999;

  picture,
  img {
    width: 100%; } }

.nav-mobile {
  left: 0;
  top: 0;
  background: $secondary;
  height: 100vh;
  width: 100%;
  position: absolute;
  padding-top: 20vh;
  opacity: 0;
  @include flex-col;
  align-items: center;
  z-index: 666;
  transform: translateY(-100%);
  @include transition;

  .nav-link {
    text-decoration: none;
    font-size: 20px;
    color: $tertiary;
    width: 100%;
    text-align: center;
    padding: 1rem 0;
    font-weight: 700;
    border-bottom: 1px solid $tertiary;
    @include transition;
    &:hover {
      background: $tertiary;
      color: $secondary; }
    &:first-of-type {
      border-top: 1px solid $tertiary; } }

  .mobile-nav-img {
    width: 50vw;
    margin: $gutterWidth auto; } }

.show-mobile-nav {
  opacity: 1;
  transform: translateY(0); }

.no-scroll {
  overflow: hidden; }

.d-none-sm {
  display: none; }

@media screen and (min-width: 1024px) {
  .d-flex-lg {
    display: flex;
    justify-conyent: center;
    align-items: center; }

  .nav-link {
    margin: 0 1rem;
    color: $primary;
    text-decoration: none;
    padding: 7px 10px;
    border: 1px solid $primary;
    cursor: pointer;
    border-radius: 50px;
    @include transition;
    &:hover {
      background: $accent;
      border: 1px solid $accent; } } }
