// Global styles

html {
  scroll-behavior: smooth; }

html,
body {
  font-family: $typeface; }

@media screen and (min-width: 667px) {
  .container {
    margin: auto;
    width: 90vw; } }

@media screen and (min-width: 1024px) {
  .container {
    margin: auto;
    width: 80vw; } }

@media screen and (min-width: 1300px) {
  .container {
    width: 1242px; } }
